<template>
  <div>
    <v-container>
      <v-row>
        <v-col>
          <v-list dense class="pa-0">
            <v-list-item v-for="(item,i) in $store.state.basket" :key="i">
                <v-list-item-content class="pa-1">                  
                  {{item.name}} 
                </v-list-item-content>  
                <v-spacer></v-spacer>                
                <v-list-item-content class="pa-1">                  
                  {{item.price}} Ft
                </v-list-item-content>                  
                <v-list-item-action class="ma-0">
                  <v-btn icon @click="deleteItem(i)"> 
                    <v-icon color="red">mdi-close-circle</v-icon>
                  </v-btn>
                </v-list-item-action>
            </v-list-item>
          </v-list>
        </v-col>
      </v-row>       
    </v-container>
    <v-footer color="warning" dark height="80">
      <v-row align="center">
        <v-col cols="1" sm="2"></v-col>
        <v-col cols="6" sm="5" md="6" >
           Összesen: {{sumPrice}} Ft
        </v-col>
        <v-col cols="4" sm="3" md="2" align="center" >
            <v-btn @click="order()">Rendelés</v-btn>
        </v-col>
        <v-col cols="1" sm="2"></v-col>
      </v-row>
    </v-footer>
<!--  Order placed -->
     <v-dialog v-model="dialog" max-width="290" persistent>
      <v-card>
        <v-card-title class="headline">Sikeres Rendelés</v-card-title>
        <v-card-text>
          Köszönjük rendelését. Amint elkészültünk vele, azzonnal értesítjük. Addig is szíves türelmét kérjük.
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="afterDialog()">
            ok
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false
  }),
  computed: {
    sumPrice() {
      return this.$store.state.basket.reduce((a,b) => a + b.price,0)
    }
  },
  methods: {
    deleteItem(i) {
      this.$store.commit('delFromBasket',{i})
      console.log(i);      
    },
    order() {
      this.dialog=true;
    },
    afterDialog() {
      this.dialog=false;
      this.$store.commit('resetBasket');      
      this.$router.push({ name : 'home'})
    }
  },
};
</script>
